"use client"; // İstemci bileşeni olarak işaretle

import Image from "next/image";
import home_page_1 from "@/public/images/home_page_1.svg";
import home_page_2 from "@/public/images/home_page_2.svg";
import home_page_4 from "@/public/images/home_page_4.svg";
import { FaSearch } from "react-icons/fa";

const Home = () => {
  return (
    <div className="relative overflow-hidden min-h-screen bg-black">
      {/* Ana arka plan */}
      <Image
        src={home_page_1}
        alt="background"
        layout="fill"
        className="object-cover z-0"
      />

      <div className="max-w-7xl mx-auto px-4">
        <div className="relative flex flex-wrap items-center justify-between">
          {/* İkincil arka plan */}
          <div className="absolute z-0 left-2 right-0">
            <Image
              src={home_page_2}
              alt="backgroundbottom"
              layout="responsive" // "responsive" kullanarak mobil uyumluluk sağlarsınız
              width={700}  // genişlik
              height={300} // yükseklik
              className="object-cover"
            />
          </div>

          <div className="relative left-0 md:left-16 z-10 ">
            <Image
              src={home_page_4}
              alt="left image"
              layout="intrinsic"
              width={200}
              height={200}
              className="object-contain"
            />
          </div>

          {/* Bölüm Butonu */}
          <div>
            <button
              className="bg-[#13a0cb] text-white px-4 py-2 
              font-bold rounded-tl-xl rounded-tr-xl"
            >
              الأقسام
            </button>
          </div>
        </div>

        {/* Arama Kutusu */}
        <div className="relative flex items-center justify-end">
          <input
            type="text"
            placeholder="للبحث السريع عن خدماتنا..."
            dir="rtl"
            className="font-asmaa p-2 placeholder-gray-600 text-black 
            bg-white border border-gray-700 rounded-tl-xl rounded-bl-xl 
            focus:outline-none sm:text-lg"
          />
          <button className="bg-[#13a0cb] text-white px-3 py-3 rounded-tr-xl rounded-br-xl">
            <FaSearch className="text-white text-xl" />
          </button>
        </div>
      </div>

      <style jsx>{`
        @media screen and (max-width: 640px) {
          /* Küçük ekranlar için ayarlar */
          .absolute.z-0.left-2.right-0 {
            transform: scale(1.2); /* Resmi biraz büyütmek için */
          }
        }
      `}</style>
    </div>
  );
};

export default Home;
